<!-- 体验报告, 文章推荐 , 活动回顾 的文章 主要用于嵌入详情页的 小图 -->
<template>
    <div>
      <ul class="recommendArticles">
            <li v-for="(item,index) in resData" :key="index" @click='enterArticle(item.id)'>
                <img :src="item.imgUrl">
                <div class="articleInfo">
                    <p class="articleTitle">{{item.title}}</p>
                    <p class="author">{{item.author}}</p>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  props: ["resData"],
  data() {
    return {};
  },
  methods: {
    enterArticle(id) {
      alert(id);
    }
  },
  created() {},
  mounted() {}
};
</script>
 <style lang='less' scoped>
@m: 750/375;
.recommendArticles li {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  margin-bottom: 20px * @m;
}

.recommendArticles img {
  display: block;
  width: 94px * @m;
  height: 94px * @m;
  border-radius: 8px * @m;
  margin-right: 16px * @m;
}
.articleInfo {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.recommendArticles .articleTitle {
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  font-size: 16px * @m;
  margin-bottom: 10px * @m;
}
.recommendArticles .author {
  font-size: 12px * @m;
  color: #c6c6c6;
}
</style>




