<template>
    <div class="authorBar">
        <!-- 左边头像区域 -->
        <div class="author">
            <div class="authorIcon" :style="'background-image: url('+resData.authorIcon+')'">
                <div v-if="resData.authorGrades==1"  class="vip1"></div>
                <div v-if="resData.authorGrades==2"  class="vip2"></div>
                <div v-if="resData.authorGrades==3"  class="vip3"></div>
            </div>
            <span>
                {{resData.authorName}}
            </span>
        </div>
        <!-- 右边关注区域 -->
        <div v-if="!resData.isFocus" class="concern" @click="fouceAuthor(resData.authorId)">
            <div class="addIcon"></div>
        </div>
    </div>

</template>
<script>
export default {
  props: ["resData"],
  data() {
    return {
      authorInfo: {}
    };
  },
  methods: {
    fouceAuthor(id) {
      alert("子组件调用" + id);
      this.$emit("focusAuthor", id);
      this.resData.isFocus = true;
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang='less' scoped>
@m: 750/375;

/* 作者栏 */
.authorBar {
  padding: 0 5px * @m;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px * @m;
}

.authorBar .author {
  display: flex;
  align-items: center;
}

.authorBar .authorIcon {
  position: relative;
  top: 0;
  display: inline-block;
  width: 40px * @m;
  height: 40px * @m;
  border-radius: 100%;
  //   background-image: url("../assets/image/touxiang.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.authorBar .authorIcon .vip1 {
  position: absolute;
  top: 32px * @m;
  width: 32px * @m;
  height: 32px * @m;
  background-image: url("../../assets/image/star.png");
  background-repeat: no-repeat;
  background-size: cover;
  left: 50%;
  transform: translateX(-50%);
}
.authorBar .authorIcon .vip2 {
  position: absolute;
  top: 32px * @m;
  width: 32px * @m;
  height: 32px * @m;
  background-image: url("../../assets/image/star.png");
  background-repeat: no-repeat;
  background-size: cover;
  left: 50%;
  transform: translateX(-50%);
}
.authorBar .authorIcon .vip3 {
  position: absolute;
  top: 32px * @m;
  width: 32px * @m;
  height: 32px * @m;
  background-image: url("../../assets/image/star.png");
  background-repeat: no-repeat;
  background-size: cover;
  left: 50%;
  transform: translateX(-50%);
}

.authorBar .author span {
  font-size: 14px * @m;
  margin-left: 10px * @m;
}

.authorBar .concern {
  width: 40px * @m;
  height: 40px * @m;
  border-radius: 100%;
  background-image: linear-gradient(-180deg, #57e6a6 0%, #3bb76e 100%);
  box-shadow: 0 0 30px * @m 0 rgba(71, 246, 143, 0.45);
  position: relative;
  top: 0;
}

.authorBar .concern .addIcon {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 16px * @m;
  height: 16px * @m;
  background-image: url("../../assets/image/+.png");
  background-repeat: no-repeat;
  background-size: cover;
}
</style>




